#projetos {
  background-color: #202020;
  color: white;
  padding: 50px;
}

.divProjetos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.projetos {
  padding-top: 93px;
}

.projetos h2, .projetos a {
  text-align: center;
}

.projetos h2 {
  padding-bottom: 70px;
}

button {
  padding: 10px 100px;
  border-radius: 25px;
  cursor: pointer;
  color: white;
  background-color: #493737;
  border: none;
  transition: ease-in-out 0.3s;
}

button:hover {
  background-color: #a16c60;
}

.projetos a {
  display: inline;
}

.pro {
  background-color: #C4C4C4;
  height: 195px;
  width: 310px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  overflow: hidden;
  transition: ease-in-out 0.3s;
}

.pro div p {
  font-family: 'Fira Sans', sans-serif;
  font-size: 20px;
  color: #C4C4C4;
}

.pro:hover {
  letter-spacing: 5px;
}

.pro div {
  background-color: #000000;
  width: 1000px;
  text-align: center;
  padding: 5% 0;
  transform: rotate(-50deg);
}

.escondido {
  opacity: 0;
  display: none;
  transition: 1000ms;
}

.projetos-itens {
  display: flex;
  flex-wrap: wrap;
  gap: 29.5px;
  width: 100%;
  justify-content: center;
}

@media (max-width:1200px) {
.projetos{
    text-align: center;
    display: flex;
    flex-direction: column;
}

#projetos {
    display: flex;
    justify-content: center;
}

.projetos-itens {
    flex-direction: column;
}

}
@media (max-width:950px) {
  .pro {
      max-width: 300px;
  }

}