.foto-i {
  width: 300px;
}

.centralizador {
  display: flex;
  justify-content: center;
  align-items: center;
}

.format {
  text-align: justify;
  width: 80%;
}

#sobre-mim {
  background-color:#141414;
  color: white;
  padding: 10px;
  font-size: 25px;
}

@media (max-width:1200px) {
  .format {
    display: flex;
    text-align: justify;
    width: 100%;
  }
  .second {
      display: none;
  }

  .ajustador-2 p {
      font-size: 15px;
  }
  .sobre-mim p {
      text-indent: 0;
  }
}




